import anUploadImg from '@/components/an-uploadImg/an-uploadImg'
export default {
	data() {
		return {
			content: [ //工作内容
				{
					text: ''
				}
			],
			text: [ //工作计划
				{
					text: ''
				}
			],
			statusCode: true, //上传成功标识
			addimgbase: true, //是否有图片标识
		}
	},
	components: {
		anUploadImg
	},
	created() {
		uni.request({
			url: this.$store.state.url + '/PCenter/GetWorlDaily',
			method: 'GET',
			data: {
				workdailyId: this.$store.state.WorkdailyId
			},
			header: {
				'Token': this.$store.state.token
			},
			success: (res) => {
				console.log(JSON.parse(res.data).Data)
			}
		});
	},
	methods: {
		//上传按钮
		imgVuex() {
			if (!this.$store.state.imgList[0]) {
				this.addimgbase = false
			} //是否上传图片
			var Plans = []
			var Contents = []
			for (var i = 0; i < this.text.length; i++) {
				Plans.push(this.text[i].text)
			}
			for (var i = 0; i < this.content.length; i++) {
				Contents.push(this.content[i].text)
			}
			uni.request({
				url: this.$store.state.url + 'PCenter/AddWorkDailyContent',
				method: 'POST',
				data: {
					WorkDailyId: this.$store.state.WorkdailyId,
					Contents: Contents,
					Plans: Plans
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					//成功回调后 判断是否进行图片上传
					if (res.statusCode == 200 && this.addimgbase) {
						this.upimg() //执行上传图片
						//执行完for上传图片后，请求改变状态
						if (this.statusCode || !this.addimgbase) {
							this.agi()
						}
					} else {//如果不上传图片直接返回列表页
						this.agi()
					}
				}
			});
		},
		//上传图片功能
		upimg: function() {

			for (var i = 0; i < this.$store.state.imgList.length; i++) {
				uni.request({
					url: this.$store.state.url + 'PCenter/UploadWorkDailyPhoto',
					method: 'POST',
					data: {
						Id: this.$store.state.WorkdailyId,
						FileStr: this.$store.state.imgList[i]
					},
					header: {
						'Token': this.$store.state.token
					},
					success: (res) => {
						if (res.statusCode != 200) this.statusCode = false
					}
				});
			}

		},
		//改变当前订单状态
		agi:function(){
			uni.request({
				url: this.$store.state.url + 'PCenter/UploadWorkDaily',
				method: 'POST',
				data: {
					Id: this.$store.state.WorkdailyId,
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					if (res.statusCode) {
						uni.navigateTo({url: '../work.4'});
					}
				}
			});
		},
		//添加工作计划
		addinput() {
			var aa = {
				text: ''
			}
			this.text.push(aa)
		},
		//删除工作计划输入框
		deletetext(i) {
			this.text.splice(i, 1)
		},
		//添加工作内容
		addinput1() {
			var aa = {
				text: ''
			}
			this.content.push(aa)
		},
		//删除内容输入框
		deletecontent(i) {
			this.content.splice(i, 1)
		}
	}

}
